.container {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 3px;
  row-gap: 3px;
  width: 90%;
  margin: 20px auto;
  text-align: end;
}

.grid-item {
  padding: 20px;
  background-color: #dc4680;
}

.grid-item:hover {
  background: #cf4278;
}

.grid-item h2 {
  font-size: 30px;
}

.grid-item p {
  font-size: 25px;
}

.details {
  font-size: 25px;
  margin: 10px 10px 20px;
}
